import React from "react";
import PropTypes from "prop-types";
import {declineNumeral} from "../../utils";

const WelcomeScreen = (props) => {
  const {errorsCount, onPlayButtonClick} = props;

  return (
    <section className="welcome">
      <div className="welcome__logo">
        <img src="img/melody-logo.png" alt=" " width="186" height="83"/>
      </div>
      <button className="welcome__button" onClick={onPlayButtonClick}>
        <span className="visually-hidden"> </span>
      </button>
      <h2 className="welcome__rules-title"> </h2>
      <p className="welcome__text"> :</p>
      <ul className="welcome__rules-list">
        <li>    .</li>
        <li>  {declineNumeral(errorsCount, ``, ``, ``)}.</li>
      </ul>
      <p className="welcome__text">!</p>
    </section>
  );
};

WelcomeScreen.propTypes = {
  errorsCount: PropTypes.number.isRequired,
  onPlayButtonClick: PropTypes.func.isRequired,
};

export default WelcomeScreen;
